import { Component, OnInit } from '@angular/core';
import { Storage } from '@ionic/storage';
import { LoaderService } from '../../services/loader.service';
import { SermonService } from '../../services/sermon.service';
import { element } from 'protractor';
import { ActivatedRoute, Router, NavigationEnd } from '@angular/router';
import { Location, formatDate } from '@angular/common';
import { ToastService } from '../../services/toast.service';
import { CommonService } from '../../services/common.service';
import { EventProviderService } from "../../services/event-provider.service";
import * as moment from 'moment';
import { Platform, MenuController, ModalController } from "@ionic/angular";
import { Device } from "@ionic-native/device/ngx";
import { AnalyticsService } from "../../services/analytics.service";
import { TranslateService } from "@ngx-translate/core";
import { ApiService } from 'src/services/api.service';
import { GeneralSearchComponent } from '../common/general-search/general-search.component';

@Component({
    selector: 'app-sermons',
    templateUrl: './sermons.page.html',
    styleUrls: ['./sermons.page.scss'],
})
export class SermonsPage implements OnInit {
    page_name: any;
    allSermons: any;
    latestSermon: any;
    popularSermons: any;
    recentSermons: any = [];
    otherSermons: any;
    showOtherSermons: boolean = false;
    latestspeaker: any;
    latesttitle: any;
    latestimage: any;
    showPopularSermons: any = false;
    showSermons: boolean = false;
    showSeries: any = false;
    sermons_url: any;
    search_sermon: any;
    enable_home: any;
    series: any[] = [];
    sermons: any[] = [];
    latest_sermon: any;
    start_time_analytics: any;
    page_id: any;
    custom_name: any;
    ch_id: any;
    user: any;
    theme: any;
    show_popular_sermons: any;
    apppage_id: any;
    link_type: any;
    alertMessage: any;
    page_in_home_tabs: any = false;
    is_wordpress_enable: boolean;
    all_series: any[];
    all_sermons: any[];
    oldDesign: boolean = false;
    deviceType: string = '';
    popular_sermon : boolean =false;
    search_sermon_placeHolder:any="Search for shows or show series";

    constructor(
        private route: ActivatedRoute,
        private storage: Storage,
        private loader: LoaderService,
        private sermonsService: SermonService,
        private router: Router,
        private apiService: ApiService,
        private location: Location,
        private toast: ToastService,
        private common: CommonService,
        private event_provider: EventProviderService,
        private platform: Platform,
        private device: Device,
        private analytics: AnalyticsService,
        private translate: TranslateService,
        private menu: MenuController,
        public modalController: ModalController,
    ) {

        // assigning data from URL Params
        this.page_id = this.route.snapshot.paramMap.get('page_id');

        this.checkIfPageOpenedInTab();
        this.getAlertTranslation();

        this.event_provider.getChurchData.subscribe(async (res: any) => {
            this.setData(res);
        });

        // event subscription, runs when event fired from common service to update if HOME Page is enabled or not
        this.event_provider.isHomeEnable.subscribe(value => {              // listening event
            this.enable_home = value.value;
        });

        if (!this.enable_home) {
            // updating home page status from variable set in common service
            // "enable_home" variable is used to switch ON or OFF the back button on Mobile View
            // If the HOME page is disabled and THIS page is selected as first landing page, there should be no BACK button
            this.enable_home = this.common.is_home_enable;
        }
    }

    async ngOnInit() {
        if (!localStorage.getItem('is_wordpress_enable')) {
            this.recentSermons = await this.sermonsService.getRecentSermons();
        }

        this.getUser();
        this.getshows(this.ch_id)
    }

    getshows(ch_id){
        if(this.oldDesign === false){
            this.apiService.getSermonSeries(this.ch_id).subscribe(
                (res: any) => {
                    if(res.latest) {
                        this.latest_sermon = res.latest;
                        if (this.latest_sermon.featured_media) {
                            this.latestimage = this.latest_sermon.featured_media;
                        } else {
                            this.latestimage = '../../assets/img/live_stream_bg_2.jpg';
                        }
                    }
                    this.series = res.series;
                    this.sermons = res.sermons;
                    this.all_series = this.series;
                    this.all_sermons = this.sermons;
                    this.loader.stopLoading();
                },
                (err: any) => {
                    console.log(err);
                }
            );
            
        }else{
            this.apiService.getshows(this.ch_id).subscribe(
                (res: any) => {
                  if (res.status) {
                    this.sermons = res.shows;
                  }
    
                  this.loader.stopLoading();
                },
                (err: any) => {
                  console.log(err);
                }
              );
        }
    
    }
    ionViewWillEnter() {
        this.ngOnInit();

        this.event_provider.getChurchData.subscribe(async (res: any) => {
            if (!(Object.keys(res).length === 0)) {
                // asking for password
                var id = this.route.snapshot.paramMap.get('page_id');
                let app_pages = res.apppages.find(x => x.id === this.page_id);
                if(app_pages){
                 
                    if(app_pages.list_view == 'true'){
                        this.oldDesign = true
                    }else{
                        this.oldDesign = false
                    }

                }

                if(app_pages){
                    this.search_sermon_placeHolder = app_pages.search_sermon_placeHolder
                    if(app_pages.show_popular_sermons == 'true'){
                        this.popular_sermon = true;
                    }else{
                        this.popular_sermon = false;
                    }
                    
                }
                if (app_pages && app_pages.password_protection_toggle === 'true') {
                    this.common.openPagePasswordModal(app_pages.page_protected_password, app_pages.selected_private_group);
                }
            }
        });

    }

    setData(res) {
        this.is_wordpress_enable = false;
        if (!(Object.keys(res).length === 0)) {

            this.theme = res.church;
            this.ch_id = res.church.ch_id;

            if (res.is_wordpress_enable === '1') {
                this.is_wordpress_enable = true;
                let sermon_page = res.apppages.filter(x => x.id === this.page_id);
                if (sermon_page.length > 0) {
                    this.apppage_id = sermon_page[0].id;
                    this.sermons_url = sermon_page[0].url;
                    this.page_name = sermon_page[0].title;
                    this.custom_name = sermon_page[0].name;

                    let interval = setInterval(() => {
                        this.translate.get('ALERTS').subscribe((res: any) => {
                            clearInterval(interval);
                        });
                    }, 2000)
                }
                if(sermon_page[0].list_view == "true"){
                    this.apiService.getshows(this.ch_id).subscribe(
                        (res: any) => {
                            if (res.status) {
                                this.sermons = res.shows;

                            }
    
                            this.loader.stopLoading();
                        },
                        (err: any) => {
                            console.log(err);
                        }
                    );
                }else{
                   // console.log("testtttttt")
                this.apiService.getSermonSeries(this.ch_id).subscribe(
                    (res: any) => {
                        if(res.latest) {
                            this.latest_sermon = res.latest;
                            if (this.latest_sermon.featured_media) {
                                this.latestimage = this.latest_sermon.featured_media;
                            } else {
                                this.latestimage = '../../assets/img/live_stream_bg_2.jpg';
                            }
                            if(this.latestimage !== "" && this.latestimage !== null && this.latestimage.includes("s3.wasabisys.com") && formatDate(this.latest_sermon.created_at,'yyyy-MM-dd','en_US') >= '2023-05-29'  ){

                                let abc =this.latestimage.split("/");
                                let folder_name = abc[abc.length-2];
                                if(this.platform.is("mobile")){
                                    this.deviceType = 'radiobase_mobile';
                                }else if(this.platform.is("tablet")){
                                    this.deviceType = 'radiobase_tablet';
                                }else{
                                    this.deviceType = folder_name;
                                }
            
                                let new_link =   this.latestimage.replace(folder_name, this.deviceType);
                                this.latestimage = new_link; 
                                this.latest_sermon['featured_media'] = this.latestimage;
                           //     console.log('latest image  is: ',this.latest_sermon,'new link is: ', this.latestimage);
                            }
                        }
                        this.series = res.series;
                        this.series.forEach(eachSeire=>{
                            if(eachSeire.image_id !== "" && eachSeire.image_id !== null && eachSeire.image_id.includes("s3.wasabisys.com") && formatDate(eachSeire.created_at,'yyyy-MM-dd','en_US') >= '2023-05-29'  ){

                                let abc = eachSeire.image_id.split("/");
                                let folder_name = abc[abc.length-2];
                                if(this.platform.is("mobile")){
                                    this.deviceType = 'radiobase_mobile';
                                }else if(this.platform.is("tablet")){
                                    this.deviceType = 'radiobase_tablet';
                                }else{
                                    this.deviceType = folder_name;
                                }
            
                                let new_link =  eachSeire.image_id.replace(folder_name, this.deviceType);
                                eachSeire['image_id'] = new_link; 
                              //  console.log('background is: ',card,'new link is: ', new_link);
                            }
                        })
                        this.sermons = res.sermons;
                        this.sermons.forEach(eachSeire=>{
                           // console.log('inside the sermonsss');
                            if(eachSeire.featured_media !== "" && eachSeire.featured_media !== null && eachSeire.featured_media.includes("s3.wasabisys.com") && formatDate(eachSeire.created_at,'yyyy-MM-dd','en_US') >= '2023-05-29'   ){

                                let abc = eachSeire.featured_media.split("/");
                                let folder_name = abc[abc.length-2];
                                if(this.platform.is("mobile")){
                                    this.deviceType = 'radiobase_mobile';
                                }else if(this.platform.is("tablet")){
                                    this.deviceType = 'radiobase_tablet';
                                }else{
                                    this.deviceType = folder_name;
                                }
                                let new_link =  eachSeire.featured_media.replace(folder_name, this.deviceType);
                               // let new_link = 'https://s3.wasabisys.com/churchbase.site/' + this.deviceType +'/' + img_name;
                                eachSeire['featured_media'] = new_link; 
                              
                            }
                        });
                     //    console.log('sermons are: ',this.sermons);
                        this.loader.stopLoading();
                    },
                    (err: any) => {
                        console.log(err);
                    }
                );
                }
               
            } else {
                this.is_wordpress_enable = false;
                let sermon_page = res.apppages.filter(x => x.id === this.page_id);
                if (sermon_page.length > 0) {
                    this.apppage_id = sermon_page[0].id;
                    this.sermons_url = sermon_page[0].url;
                    this.page_name = sermon_page[0].title;
                    this.custom_name = sermon_page[0].name;
                    this.show_popular_sermons = sermon_page[0].show_popular_sermons;

                    // getting sermons from URL
                    if (this.sermons_url.includes('.rss') || this.sermons_url.includes('-rss')) {
                        // if url is .rss feed
                        this.getRss(this.apppage_id, this.ch_id)
                    } else {
                        this.getSermonsFromWordpress(this.sermons_url);
                        // get translated alert message
                    }
                    let interval = setInterval(() => {
                        this.translate.get('ALERTS').subscribe((res: any) => {
                            clearInterval(interval);
                        });
                    }, 2000)
                }
            }
        } else {
            // this.event_provider.getChurchData.subscribe(async (res: any) => {})
        }
    }

    checkIfPageOpenedInTab() {
        // chekcing if page is opened in home tabs or simple
        let current_url = new URL(window.location.href);
        let page_href = current_url.pathname;
        if (page_href.includes('home-tabs')) {
            this.page_in_home_tabs = true;
        } else {
            this.page_in_home_tabs = false;
        }
    }

    getsermonpwa(id_series) {
        this.loader.presentLoading().then(() => {
            // navigating to detail page
            this.router.navigate(['/show-detail', { id: id_series, page_id: this.page_id }]);
            this.loader.stopLoading();
        });
    }

    getAlertTranslation() {
        // get translated alert message
        let interval = setInterval(() => {
            this.translate.get('ALERTS').subscribe((res: any) => {
                this.alertMessage = res;
                clearInterval(interval);
            });
        }, 2000);
    }

    getUser() {
        // getting user from local storage
        this.storage.get('user').then((val: any) => {
            if (val != null && val != 'guest') {
                this.user = val;
            }
        });
    }

    getRss(id, church_id) {
        this.loader.presentLoading().then(() => { });
        this.link_type = "rss";
        let self = this;
        // fetching data through service
        this.sermonsService.get_rss(id, church_id).subscribe((res: any) => {
            self.allSermons = res.audio.channel.item;

            if (self.allSermons.length != 0) {
                this.setApiData(this.link_type);
            }
            this.loader.stopLoading();
        });
    }

    getSermonsFromWordpress(base_url) {
        this.link_type = "sermon";
        this.loader.presentLoading().then(() => { });
        let self = this;
        this.sermonsService.getAllSermons(base_url).subscribe(async (res: any) => {
            if (res) {
                let data = res.data;
                self.allSermons = data.item;
                if (self.allSermons.length != 0) {
                    this.setApiData(this.link_type);
                }
                this.loader.stopLoading();
            } else {
                this.loader.stopLoading();
            }
        });
    }

    setApiData(link_type) {
        let self = this;
        // SET LATEST SERMON
        self.latestSermon = self.allSermons[0];
        self.latesttitle = self.latestSermon.title;
        if (link_type === 'sermon') {
            self.latestspeaker = "";
            // self.latestspeaker = self.latestSermon['itunes:author'];
            if (self.latestSermon['itunes:image']) {
                self.latestimage = self.latestSermon['itunes:image']['$'].href;
            } else {
                self.latestimage = self.latestSermon.sermon_image !== '' ? self.latestSermon.sermon_image : '../../assets/img/live_stream_bg_2.jpg';
            }
            if(this.latestimage !== "" && this.latestimage !== null && this.latestimage.includes("s3.wasabisys.com")  && formatDate(this.latestSermon.created_at,'yyyy-MM-dd','en_US') >= '2023-05-29'  ){

                let abc = this.latestimage.split("/");
                let folder_name = abc[abc.length-2];
                if(this.platform.is("mobile")){
                    this.deviceType = 'radiobase_mobile';
                }else if(this.platform.is("tablet")){
                    this.deviceType = 'radiobase_tablet';
                }else{
                    this.deviceType = 'logo';
                }

                let new_link = this.latestimage.replace(folder_name, this.deviceType);
                // let new_link = 'https://s3.wasabisys.com/churchbase.site/' + this.deviceType +'/' + img_name;
                this.latestimage = new_link;
            }
            this.allSermons.splice(0, 1);
            let all = this.allSermons;
            for (let x = 0; x < all.length; x++) {
                if (all[x]['itunes:image']) {
                    all[x].img = all[x]['itunes:image']['$']['href'];
                } else {
                    all[x].img = all[x].img_series;
                }
            }
            let recentSermon = [];
            for (let x = 0; x < this.recentSermons.length; x++) {
                if (this.allSermons.some(person => person.title === this.recentSermons[x].title)) {
                    recentSermon.push(this.recentSermons[x]);
                }
            }
            this.sermonsService.storeRecentSermons(recentSermon);
            this.recentSermons = recentSermon;
            this.allSermons.splice(0, 1);
            self.otherSermons = all;
        } else if (link_type === 'rss') {
            self.latestspeaker = "";
            self.latestimage = self.latestSermon.thumbnail ? self.latestSermon.thumbnail : '../../assets/img/live_stream_bg_2.jpg';
            if(this.latestimage !== "" && this.latestimage !== null && this.latestimage.includes("s3.wasabisys.com") && formatDate(this.latestSermon.created_at,'yyyy-MM-dd','en_US') >= '2023-05-29' ){

                let abc = this.latestimage.split("/");
                let img_name = abc[abc.length-1];
                if(this.platform.is("mobile")){
                    this.deviceType = 'radiobase_mobile';
                }else if(this.platform.is("tablet")){
                    this.deviceType = 'radiobase_tablet';
                }else{
                    this.deviceType = 'logo';
                }

                let new_link = 'https://s3.wasabisys.com/churchbase.site/' + this.deviceType +'/' + img_name;
                this.latestimage['background'] = new_link; 
              //  console.log('background is: ',card,'new link is: ', new_link);
            }
            this.allSermons.splice(0, 1);
            self.otherSermons = this.allSermons;
        }
        if (this.show_popular_sermons === 'true') {
            // GET ALL UNIQUE SERIES
            let newPopularArr = [];
            self.allSermons.forEach((item, index) => {
                if (item.series) {
                    if (item.series != '') {
                        if (newPopularArr.findIndex(i => i.series == item.series) === -1) {
                            item['search_array'] = [];
                            newPopularArr.push(item);
                            // to save all titles of sermons the series has
                            this.allSermons.forEach((item, index) => {
                                newPopularArr.map(x => {
                                    if (x.series == item.series) {
                                        x.search_array.push(item.title);
                                    }
                                })
                            });
                        }
                    }
                }
            });
            self.popularSermons = newPopularArr;
        }
    }

    getSermonSeries(id) {
        this.router.navigate(['/all-shows', {
            id: id,
            ch_id: this.ch_id
        }]);
    }

    getlatestsermon(item = null, publishDate = null) {
        if (this.is_wordpress_enable) {
            this.router.navigate(['/show-detail', {
                id: item,
                page_id: this.page_id
            }]);
        } else {
            if (this.link_type === "rss" && item) {
                this.router.navigate(['/show-detail', { id: item, page_id: this.page_id }]);
            } else if (item === null && publishDate) {
                this.router.navigate(['/show-detail', { date: publishDate, page_id: this.page_id }]);
            } else if (publishDate) {
                this.router.navigate(['/show-detail', { idseries: item, date: publishDate, page_id: this.page_id }]);
            } else if (item && !publishDate) {
                this.router.navigate(['/all-shows', { id: item }]);
            } else {
                this.router.navigate(['/show-detail', { id: 'latest', page_id: this.page_id }]);
            }
        }
    }

    // navigating to back page
    gotobackPage() {
        if (this.ch_id === '1000') {
            this.menu.toggle();
        } else {
            this.location.back();
        }
    }

    setAnalytics() {
        let dev_id = '';
        let device = 'web';
        // preparing device data

        dev_id = this.device.uuid;
        if (dev_id) {
            device = this.platform.platforms()[0];
        }


        // preparing duration data
        let end_time_analytics = moment();
        var duration: any = moment.duration(end_time_analytics.diff(this.start_time_analytics)).asSeconds();

        // preparing user data
        let userId = this.user ? this.user.user.id : 'guest';

        // preparing data for analytics
        const body = JSON.stringify({
            church_id: this.ch_id,
            latitude: this.theme.latitude ? this.theme.latitude : "",
            longitude: this.theme.longitude ? this.theme.longitude : "",
            timespent: duration,
            module: this.custom_name,
            custom_name: this.page_name,
            ip_address: "",
            device: device,
            device_id: dev_id,
            client_type: userId,
            event_type: ""
        });
        // calling function from service to update Analytics Data
        this.analytics.setAnalytics(body).subscribe((res: any) => {

        })
    }

    ionViewDidLeave() {
        this.setAnalytics();
    }
    ionViewDidEnter() {
        // setting start time for analytics
        this.start_time_analytics = moment();
        // this.event_provider.hidefooter(true, 'other');
    }
    searchContent(event) {
        if (event.detail.value.length > 1) {
            this.apiService.searchInShow(this.theme.ch_id, 1, event.detail.value).subscribe((res: any) => {
                if (res.status) {
                    this.series = res.series;
                }
                if (res.status) {
                    this.sermons = res.sermons;
                }

            })
        } else {
            this.series = this.all_series;
            this.sermons = this.all_sermons;
        }
    }
    search() {
        this.filterModal()

    }
    async filterModal() {
        const modal = await this.modalController.create({
            component: GeneralSearchComponent,
            cssClass: 'BibleModal',
            componentProps: {
                PageId: this.page_id
            }
        });
        modal.onDidDismiss().then(res => {

        });
        return await modal.present();
    }
}
